body {
  height: 100vh;
  background-image: url("bg-feedback-form.8f1655b9.jpg");
  background-repeat: no-repeat;
  background-size: cover;
}

.link-arrow-left {
  color: #4466fd;
  margin-bottom: 20px;
  margin-left: 80%;
  display: inline-block;
}

@media (min-width: 768px) {
  .link-arrow-left {
    margin-left: 80%;
  }
}

@media (min-width: 1536px) {
  .link-arrow-left {
    margin-left: 27%;
  }
}

.feedback-form {
  max-width: 480px;
  backdrop-filter: blur(10px);
  background: rgba(0, 0, 0, .2);
  border: 1px solid rgba(0, 0, 0, .12);
  border-radius: 4px;
  flex-direction: column;
  padding: 12px;
  display: flex;
}

@media (min-width: 768px) {
  .feedback-form {
    margin: 0 auto;
  }
}

@media (min-width: 1536px) {
  .feedback-form {
    max-width: 660px;
    padding: 20px 30px;
  }
}

.feedback-form label {
  text-align: center;
  color: #e4e4e4;
  flex-direction: column;
  margin-bottom: 16px;
  display: flex;
}

.feedback-form button {
  color: #e4e4e4;
  backdrop-filter: blur(10px);
  font: inherit;
  cursor: pointer;
  background-color: rgba(68, 102, 253, .6);
  border: 1px solid #7c7c7c;
  border-radius: 4px;
  padding: 8px 12px;
}

input, textarea {
  font: inherit;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid #7c7c7c;
  border-radius: 4px;
  padding: 8px;
}

textarea {
  resize: none;
}

